import React, { useState, useRef } from 'react'
import styled from 'styled-components'
import { useSelector, useDispatch } from 'react-redux'
import { RootState } from 'store/'

import color from '../components/color-css/color'
import FormStep2 from '../components/FormStep2'
import FormStepIndicator from '../components/FormStep'
import Layout from '../components/layout'
import FormHighOrder from '../components/FormHighOrder'
import { next, backTo, writeData } from '../components/FormHighOrder/features/index'
import { logingObject } from '../components/logging'
import Membership from "../components/Membership"
import FormStep2AboutYou from "../components/FormStep2AboutYou"

const FormStepIndicatorPositioningDiv = styled.div`
  position: absolute;
  left: 22%;
  top: 360px;
  box-sizing: border-box;
  border-radius: 5px;
  box-shadow: 0px 20px 50px #012F571A;
  background-color: ${color.white};
`
const ContainerMain = styled.div`
    max-width: 1440px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0px 92px;
    margin-top: 168px
`

const IndexPage = () => {
  const dispatch = useDispatch()

  const validations: boolean[] = [
    useSelector((state: RootState) => state.FormHighOrder.pageData[0].isValidated),
    useSelector((state: RootState) => state.FormHighOrder.pageData[1].isValidated),
    useSelector((state: RootState) => state.FormHighOrder.pageData[2].isValidated),
    useSelector((state: RootState) => state.FormHighOrder.pageData[3].isValidated),
  ]

  const onPage = useSelector((state: RootState) => state.FormHighOrder.onPage)

  const [currentPage, setCurrentPage] = useState(0)

  const formStepRef = useRef<HTMLDivElement>(null)

  const handleMoveToNextFormFromPage = (itemNumber: number, formData: any) => {
    dispatch(next())
    dispatch(writeData(
      {
        pageId: itemNumber,
        isValidated: true,
        data: { formData }
      }
    ))
  }

  {
    console.log('validations on Page 0 ' + validations[0])
    console.log('validations on Page 0 ' + onPage)
  }

  return (
    <Layout>
      <FormHighOrder >
        <FormStepIndicatorPositioningDiv>
          <FormStepIndicator
            onItemStepClickedListenerProp={(itemNumber) => {
              console.log('GIANG: Just clicked ' + itemNumber)
              if (itemNumber < currentPage) {
                dispatch(backTo(itemNumber))
              } else if (itemNumber > currentPage) {
                setCurrentPage(itemNumber)
              }
            }}
            activateNextItemStepProp={
              validations[currentPage + 1]
            }

            ref={formStepRef}
          />
        </FormStepIndicatorPositioningDiv>
        <ContainerMain>
          {/* <FormStep1
            onFormValidatedProp={(wasForm1Validated) => {
              dispatch(writeData(
                {
                  pageId: 0,
                  isValidated: wasForm1Validated,
                  data: {}
                }
              ))
            }}
            onClickOnContinueButtonProp={(formData: any) => {
              logingObject(formData)
              handleMoveToNextFormFromPage(0, formData)

              const childs = formStepRef?.current?.children
              childs && childs.item(1) &&
              (childs.item(1) as unknown as HTMLAnchorElement).click()
            }}
          /> */}
          <FormStep2AboutYou 
            // onFormValidatedProp={(wasForm1Validated) => {
            //   dispatch(writeData(
            //     {
            //       pageId: 1,
            //       isValidated: wasForm1Validated,
            //       data: {}
            //     }
            //   ))
            // }}
            // onClickOnContinueButtonProp={(formData: any) => {
            //   logingObject(formData)
            //   handleMoveToNextFormFromPage(1, formData)

            //   const childs = formStepRef?.current?.children
            //   childs && childs.item(1) &&
            //   (childs.item(1) as unknown as HTMLAnchorElement).click()
            // }}
          />
          <Membership/>
        </ContainerMain>
      </FormHighOrder>
    </Layout>
  )
}
export default IndexPage
