import React, { useState } from 'react'
import styled from 'styled-components'

import colors from '../color-css/color'
import img from '../assets/powered_by_google_on_white.png'

export type Props = {
    isVisible: boolean,
    value?: (input: string) => void,
    renderingSuggestions: string[],
    footerContent?: string
}

interface DropdownContainer {
    isVisible: boolean,
}

const DropdownContainer = styled.div<DropdownContainer>`
    display: ${props => props.isVisible === true ? 'block' : 'none'};
    position: relative;
    margin-top: 1.25rem;
    width: 40.9375rem;
    height: 14.0625rem;
    background: ${colors.white} 0% 0% no-repeat padding-box;
    box-shadow: 0px 0px 50px #012F571A;
    border-radius: 10px;
    &:before {
        content: "";
        position: absolute;
        left: 19.25rem;
        top: -1.25rem;
        width: 0;
        height: 0;
        border-top: 0.9375rem solid transparent;
        border-right: 0.9375rem solid #fff;
        border-bottom: 0.875rem solid transparent;
        -webkit-transform: rotate(90deg);
        -ms-transform: rotate(90deg);
        transform: rotate(90deg);
`

const DropdownFooter = styled.div`
    position:absolute;
    bottom:0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: space-between;
    width: 90%;
    height: 3.75rem;
    margin: auto;
    border-top: 1px solid ${colors.grey_dark};
`

const DropdownFooterContent = styled.p`
    font-family: museo-sans, sans-serif;
    font-weight: 500;
    font-style: italic;
    font-size: 1rem;
    color: ${colors.grey_darker};
    margin-top: 1rem;
`

const DropdownFooterImg = styled.span`
    margin-top: 1.1875rem;
`

const DropdownSuggestWrapped = styled.div`
    overflow: auto;
    height: 8.75rem;
    padding: 1.5625rem 1.5625rem 0 2.5rem;
    margin-right: 1rem;
    margin-top: 0.625rem;
    ::-webkit-scrollbar {
        width: 0.8125rem;
      }
    ::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px #EFEFEF; 
    border-radius: 10px;
    }
    ::-webkit-scrollbar-thumb {
    background: #EFEFEF; 
    border-radius: 10px;
    }
`

interface DropdownSuggestContent {
    value?: (input: string) => string,
}

const DropdownSuggestContent = styled.p<DropdownSuggestContent>`
    font-family: museo-sans, sans-serif;
    font-weight: 500;
    font-size: 1rem;
    line-height: 25px;
    color: ${colors.grey_darker};
    cursor: pointer;
`

export const Dropdown: React.FC<Props> = (props: any) => {
    const [content, setContent] = useState('');
    // let stringToGoIntoTheRegex = inputContent
    //       let regex = new RegExp("" + stringToGoIntoTheRegex + "", "gi")
    //       let suggestString = each.description.match(regex)
    //       console.log(suggestString)
    //       const part1 = each.description.substring(0, suggestString[0][1])
    //       const part2 = each.description.substring(suggestString[0][1], each.description.length)
    //       // return (
    //       //   <span> <strong>{part1}</strong>{part2}</span>
    //       // )

    const listItem = props.renderingSuggestions?.map((each: string) => {
        return(
            <p key={each}>{each}</p>
        )
    })
    const handleContent = (event: any) => {
        event.preventDefault();
        setContent(event.target.textContent)
    }
    return (
        <DropdownContainer isVisible={props.isVisible}>
            <DropdownSuggestWrapped>
                <DropdownSuggestContent value={props.value ? props.value(content) : {}}
                    onClick={handleContent}>
                    {listItem}
                </DropdownSuggestContent>
            </DropdownSuggestWrapped>
            <DropdownFooter>
                <DropdownFooterContent>{props.footerContent}</DropdownFooterContent>
                <DropdownFooterImg><img src={img} /></DropdownFooterImg>
            </DropdownFooter>
        </DropdownContainer>
    )
}

export default Dropdown
